#intro{
    height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative; /* Added positioning relative to act as a reference for absolute positioning */
}
.portrait{
    position: absolute;
    right: 0;
    z-index: -1;
    object-fit: cover;
    height: 50vh;
}

.introContent{
    height: 100vh;
    width: 100vh;
    padding: 2rem;
    font-size: 4rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.hello{
    font-size: 2rem; 
    font-weight: 100;
}

.introName{
    color: yellow;
}

.introPara{
font-size: large;
font-weight: 100;
}