.navbar {
    height: 5rem;
    width: 100vW;
    max-width: 100rem;
    margin: auto;
    padding: 1rem 2rem;
    display: flex;
    align-items:center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 3;
}

.logo{
    object-fit:cover;
    height: 3rem;

}

.desktopMenu {
    display: flex;
    align-items: center;
}

.desktopMenuListItem{
    margin: 0 1.75rem;
    cursor: pointer;
}

.desktopMenuListItem:hover{
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;
}

.desktopMenuImg{
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 1rem;   
}

.desktopMenuBtn {
    background: white;
    color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    border-radius:2rem ;
}